<template>
  <div id="app">
    <div class="container-fluid">
      <Header school='东北大学'></Header>
      <ProjectTitle :data="projectInfo"></ProjectTitle>
      <TeamTab :data="tabData" :active-index="1"></TeamTab>
      <div class="container">
        <div class="bar">
          <div class="title">加入团队</div>
          <el-form :inline="true" class="demo-form-inline pull-right">
            <el-form-item>
              <el-input placeholder="团队名称或队长姓名" v-model="searchKey">
                <i slot="suffix" class="iconfont iconqitasousuo" @click="loadData"></i>
              </el-input>
            </el-form-item>
            <el-form-item>
              <el-button class="pull-left" type="primary" @click="createTeam">我要组队</el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="main">
          <div class="team-box" v-for="obj in teamData" v-bind:key="obj.id">
            <div class="logo"></div>
            <div class="name">{{obj.teamName}}</div>
            <div class="count">当前人数 {{obj.currentMemberCount}}人</div>
            <el-button v-if="obj.applicableTeamStatus !== 1" type="primary" @click="applyJoin(obj.teamId)">申请加入</el-button>
            <el-button v-else @click="goTeam(obj.teamId)">已申请</el-button>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      title="创建团队"
      :visible.sync="createTeamDialogVisible"
      width="468px"
      center>
      <div class="content">
        <el-form ref="createTeamForm" :model="createTeamForm" label-width="90px">
          <div class="head-portrait">
            <div class="model" @click="$refs.headPortrait.click()">上传头像</div>
            <input style="display:none" type="file" name="fileUpload" @change="uploadPhoto($event)" ref="headPortrait" >
          </div>
          <el-form-item class="mt-5" label="团队名称：">
            <el-input v-model="createTeamForm.name"></el-input>
          </el-form-item>
          <el-form-item label="团队人数：">
            <span v-if="projectInfo.minCount && projectInfo.maxCount">{{projectInfo.minCount}}-{{projectInfo.maxCount}}人</span>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button style="width:198px;" type="primary" @click="createTeamSave">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import ProjectTitle from '../../../../components/school-training/ProjectTitle.vue'
import TeamTab from '../../../../components/school-training/TeamTab.vue'
export default {
  components: {
    Header,
    ProjectTitle,
    TeamTab
  },
  name: 'app',
  mounted () {
    this.loadData()
  },
  data () {
    return {
      projectInfo: {},
      createTeamDialogVisible: false,
      createTeamForm: {
        logoUrl: '',
        name: ''
      },
      searchKey: '',
      tabData: [{
        name: '选择项目',
        url: 'course-detail.html'
      }, {
        name: '组队',
        url: 'team-search.html'
      }, {
        name: '任务裁剪',
        url: ''
      }, {
        name: '人员管理',
        url: ''
      }],
      teamData: []
    }
  },
  methods: {
    loadData () {
      this.loadProjectInfo()
      this.loadTeamList()
    },
    loadTeamList () {
      let me = this
      let projectId = localStorage.getItem('projectId')
      let courseId = localStorage.getItem('courseId')
      let grade = localStorage.getItem('grade')
      this.$http.post('/teambuild/queryapplicableteamlist', {
        courseId: courseId,
        grade: grade,
        projectId: projectId,
        teamOrLeaderName: me.searchKey
      }, res => {
        if (res.success) {
          me.teamData = res.data
        }
      })
    },
    loadProjectInfo () {
      let me = this
      let projectId = localStorage.getItem('projectId')
      let courseId = localStorage.getItem('courseId')
      this.$http.post('/teambuild/queryprojectinfo', {
        projectId: projectId,
        courseId: courseId
      }, res => {
        if (res.success) {
          me.projectInfo = res.data
          me.projectInfo.courseName = localStorage.getItem('courseName')
        }
      })
    },
    createTeam () {
      this.createTeamDialogVisible = !this.createTeamDialogVisible
      // window.location = 'team-my.html'
    },
    createTeamSave () {
      let me = this
      let projectId = localStorage.getItem('projectId')
      let courseId = localStorage.getItem('courseId')
      let grade = localStorage.getItem('grade')
      this.$http.post('/teambuild/buildteam', {
        logoUrl: this.createTeamForm.logoUrl,
        teamName: this.createTeamForm.name,
        courseId: courseId,
        projectId: projectId,
        grade: grade
      }, res => {
        if (res.success) {
          me.createTeamDialogVisible = false
          window.location = 'team-my.html'
        }
      })
    },
    applyJoin (teamId) {
      let me = this
      this.$http.post('/teambuild/applyjointeam', {
        teamId: teamId
      }, res => {
        if (res.success) {
          this.$message('已提交申请')
          me.loadData()
        }
      })
    },
    uploadPhoto (event) {
      console.log(1)
      let me = this
      let file = event.target.files[0]
      let formData = new FormData()
      formData.append('file', file)
      this.$http.uploadFile('/teambuild/uploadimgforteam', formData, res => {
        if (res.success) {
          me.$alert('上传成功！', '提示', {
            confirmButtonText: '确定'
          })
          me.createTeamForm.logoUrl = res.data
        } else {
          // 返回错误的处理
        }
      })
    },
    goTeam (teamId) {
      localStorage.setItem('teamId', teamId)
      window.location = 'team-view.html'
    }
  }
}
</script>

<style scoped lang="scss">
  .team-box{
    cursor:pointer;
    position:relative;
    float:left;
    padding-top:22px;
    padding-bottom:19px;
    width:171px;
    background:rgba(245,245,245,1);
    border:1px solid rgba(235, 235, 235, 1);
    border-radius:5px;
    text-align: center;
    margin-right:40px;
    margin-bottom:40px;
    margin-top:24px;
    .logo{
      width:105px;
      height:105px;
      background:rgba(218,218,218,1);
      border-radius:50%;
      margin: 0 auto;
    }
    .name{
      font-size:14px;
      font-family:PingFang-SC-Bold;
      font-weight:bold;
      color:rgba(102,102,102,1);
      margin-top:14px;
    }
    .count{
      font-size:12px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(102,102,102,1);
      margin-top:8px;
    }
    .el-button{
      margin-top:5px;
      padding:8px 23px;
    }
  }
  .head-portrait{
    margin: 0px auto;
    width:116px;
    height:116px;
    background-image: url("../../assets/img/default-head.png");
    border-radius:58px;
    .model{
      width:100%;
      height:100%;
      background-color: #000000;
      border-radius:58px;
      opacity:0.5;
      display:none;
      z-index:101;
      text-align: center;
      padding-top:45px;
      font-size:14px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(255,255,255,1);
      cursor:pointer;
    }
  }
  .head-portrait:hover{
    .model{
      display:block;
    }
  }
</style>
